<template>
       <div id="contextmenu"
       ref="targetElement"
           v-show="menuVisible"
           class="menu">
            <div :class="{contextmenu__item:true,contextmenu__isEdit:item.isEdit?false:true}"
            @click="menuClick(CurrentRow,index)"
            v-for="(item,index) in menu">
            {{ item.name }}
            </div>
        </div>
</template>

<script>
export default {
    props:{
       menu:Array
    },
    data () {
        return {
        menuVisible:false,
        CurrentRow:null, //
        menudata:null, //
        Indexindex:null,// index
        }
    },
    methods:{
        //
      isShow(row, column, event) {
      this.testModeCode = row.testModeCode
      this.menuVisible = false //  n true
      this.menuVisible = true // 
      event.preventDefault() //
      this.CurrentRow = row
      var menu = document.querySelector('.menu')
      this.styleMenu(menu)
      },
      foo(e) {
        const clickedElement = e.target;
        if (this.$refs.targetElement && !this.$refs.targetElement.contains(clickedElement)) {
        //  targetElement 
         this.menuVisible = false
         document.removeEventListener('click', this.foo)
         return
        } 
        //  
        if (this.menudata.isEdit) {
         this.menuVisible = false
         document.removeEventListener('click', this.foo)
         return
        }
    },
    styleMenu(menu) {
      if (event.clientX > 1800) {
        menu.style.left = event.clientX - 100 + 'px'
      } else {
        menu.style.left = event.clientX + 1 + 'px'
      }
      document.addEventListener('click', this.foo) 
      // documentfoo
      if (event.clientY > 700) {
        menu.style.top = event.clientY - 30 + 'px'
      } else {
        menu.style.top = event.clientY - 10 + 'px'
      }
    },
    menuClick(row,index){
        // 
        document.addEventListener('click', this.foo) 
         this.menudata = this.menu[index]
         console.log(this.menudata);
        if (!this.menudata.isEdit) {
            // this.menuVisible = false
            return
          }
          //
          this.$emit(this.menudata.methodsname,row)
        }
    }
}
</script>
 
<style lang = "scss" scoped>

.menu {
  position: absolute;
  background-color: #fff;
  width: 100px;
  /*height: 106px;*/
  font-size: 12px;
  color: #444040;
  border-radius: 4px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  white-space: nowrap;
  z-index: 1000;
}

.contextmenu__item {
  display: block;
  line-height: 34px;
  text-align: center;
  position: relative;
}

.contextmenu__isEdit{
 background-color:#ccc
}

.contextmenu__item:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.contextmenu__item:hover {
  cursor: pointer;
  background: #66b1ff;
  border-color: #66b1ff;
  color: #fff;
}

.contextmenu__isEdit:hover{
 cursor:no-drop;
 background-color:#ccc;
 border-color: #ccc;
 color: #434040;
}

.ssss{
    position: absolute;
    border:1px solid #000;
    background-color: #fff;
    left:100%;
    top:0;
}

</style>