<template>
    <div>
        <div class="header">
            <h1>nav</h1>
            <el-button style="padding:10px 20px;" @click="pamad"></el-button>
            <el-button style="padding:10px 20px;" @click="zanting"></el-button>
        </div>
        <div ref="parentDiv" class="box1">
        </div>
        <div class="text"><h1>:</h1></div>
        <div class="box2"></div>
        <div class="text"><h1>:</h1></div>
        <div class="box3"></div>
        <div class="foter"></div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            arr:[]
        }
    },
    methods:{
        generateRandomHex() {
            const characters = '0123456789ABCDEF';
            let result = '';
            for (let i = 0; i < 6; i++) {
                const randomIndex = Math.floor(Math.random() * characters.length);
                result += characters.charAt(randomIndex);
            }
            console.log(result);
            return result;
            },
        // 
        suiji(){
            const parentDiv = this.$refs.parentDiv;
            while (parentDiv.firstChild) {
                parentDiv.removeChild(parentDiv.firstChild);
            }
            let ss = this.generateRandomHex()
            parentDiv.style.border = `30px dashed #${ss}`
            this.arr= []
            this.$nextTick(() => {
                for (let index = 0; index < 6; index++) {  let guid =  this.generateRandomHex()
                this.arr.push(guid)
                let childDiv =null
                childDiv = document.createElement("div");
                childDiv.id= 'id'+ guid
                childDiv.style.height = '100%'
                childDiv.style.border = `30px dashed 
                #${guid}`
                // childDiv.style.margin= '10px'
                childDiv.style.padding= '10px'
                if (index==0) {
                    parentDiv.appendChild(childDiv);
                }else{
                    console.log(this.arr);
                    console.log(document.getElementById( 'id' + this.arr[index-1]));
                    document.getElementById('id' + this.arr[index-1]).appendChild(childDiv);
                }
                }
                    });
        },
        pamad(){
            clearTimeout(this.setval); // 
            this.setval =  setInterval(() => {
                this.suiji()
            }, 100);
        },
        zanting(){
            clearInterval(this.setval) 
        }
    },
    created(){
        // for (let index = 0; index < 10; index++) {
        //     let color =  this.generateRandomHex()
        //     this.arr.push(color)
        // }
    },
    mounted(){
       this.suiji()
        
    }

}
</script>
 
<style lang = "scss" scoped>
    *{
        padding: 0;
        margin: 0;
        list-style: none;
    }
    .header{
        // width: 100%;
        height: 100px;
        text-align: center;
        background-color: rgba($color: #e65151, $alpha: 0.5);
    }
    .text{
           height: 300px;
           background-color: #ccc;
           text-align: center;
           
    }
    .box1{
        height: 600px;
        background:url(../../assets/cd-background-5.jpg) no-repeat center top fixed;
        background-size: cover;
        border: 30px dashed #aaa;
        padding: 10px;
        background-clip: border-box;
        // 
    }
    .box2{
        height: 600px;
        background:url(../../assets/cd-background-2.jpg) no-repeat center top fixed ;
        background-size: cover;
    }
    .box3{
        height: 600px;
        background:url(../../assets/cd-background-6.jpg) no-repeat center top fixed ;
        background-size: cover;
    }
    .foter{
        height: 200px;
    }
</style>