<template>
    <div>
        <el-table
  border
  :data="tableData"
>
  <el-table-column prop="name" label="Name" align="center">
    <template slot-scope="scope">
      <el-input
        v-if="scope.$index === 0"
        v-model="scope.row.name"
        maxlength="30"
        placeholder="Required"
      />
      <span v-else v-text="scope.row.name"></span>
    </template>
  </el-table-column>
  <el-table-column prop="inIp" label="Internal IP" align="center">
    <template slot-scope="scope">
      <el-select
        v-if="scope.row.isEdit"
        v-model="scope.row.inIp"
        placeholder="Required"
      >
        <el-option
          v-for="(item,index) in internalIpList"
          :key="index"
          :label="item"
          :value="item"
        />
      </el-select>
      <span v-else v-text="scope.row.inIp"></span>
    </template>
  </el-table-column>
  <el-table-column prop="inPort" label="Internal Port" align="center">
    <template slot-scope="scope">
      <el-input
        v-if="scope.row.isEdit"
        v-model="scope.row.inPort"
        maxlength="20"
        placeholder="Required"
      />
      <span v-else v-text="scope.row.inPort"></span>
    </template>
  </el-table-column>
  <el-table-column prop="outPort" label="External Port" align="center">
    <template slot-scope="scope">
      <el-input
        v-if="scope.row.isEdit"
        v-model="scope.row.outPort"
        maxlength="20"
        placeholder="Required"
      />
      <span v-else v-text="scope.row.outPort"></span>
    </template>
  </el-table-column>
  <el-table-column prop="enabled" label="Status" align="center">
    <template slot-scope="scope">
      <el-select v-if="scope.row.isEdit" v-model="scope.row.enabled">
        <el-option label="Enabled" :value="1"/>
        <el-option label="Disabled" :value="0"/>
      </el-select>
      <span v-else v-text="scope.row.enabled === 1 ? 'Enabled' : 'Disabled'"></span>
    </template>
  </el-table-column>
  <el-table-column label="Operation" align="center" class-name="small-padding fixed-width">
    <template slot-scope="scope">
      <el-button
        v-if="scope.$index === 0"
        size="mini"
        type="text"
        style="color: #2090c1"
        @click="handleAdd(scope.row)"
      >Add
      </el-button>
      <el-button
        v-if="scope.$index !== 0"
        size="mini"
        type="text"
        style="color: #2090c1"
        @click="handleUpdate(scope.row)"
      >{{ scope.row.isEdit ? 'Apply' : 'Edit' }}
      </el-button>
      <el-button
        v-if="scope.$index !== 0"
        size="mini"
        type="text"
        style="color: #F56C6C"
        @click="handleDelete(scope.row)"
      >{{ scope.row.isEdit ? 'Cancel' : 'Delete' }}
      </el-button>
    </template>
  </el-table-column>
        </el-table>
		<button @click="xinz"></button>
    </div>
</template>

<script>
export default {
	data() {
	    return {
	    	tableData: [],
     		internalIpList: [5,4,1],
			resdata:[{
	            name: '',
	            inIp: '',
	            inPort: null,
	            outPort: null,
	            enabled: null,
	            isEdit: true
			},{
	            name: '',
	            inIp: 'zs',
	            inPort: '<inPory></inPory>',
	            outPort: null,
	            enabled: 1,
	          },
			  {
	            name: '',
	            inIp: 'ls',
	            inPort: null,
	            outPort: '<outPort></outPort>',
	            enabled: 0,
	          },]
	    }
	},
	created(){
		this.getReverseShellList(1)

	
	},
	methods: {
		// 
		getReverseShellList(e) {
			this.resdata.forEach((item,index) => {
				if (index==0) {
					item['isEdit'] = true
				}else{
					item['isEdit'] = false
				}
				
			})
			this.tableData = this.resdata
	    },
	    // 
	    handleAdd(row) {
	      for (let key in row) {
	      	// 
	        if (row[key] === '' || row[key] === null || typeof row[key] === 'undefined') {
	          return
	        }
	      }
	    //   const params = {
	    //     name: row.name,
	    //     inIp: row.inIp,
	    //     inPort: parseInt(row.inPort),
	    //     outPort: parseInt(row.outPort),
	    //     enabled: row.enabled,
		// 	isEdit:false
	    //   }
			// this.resdata.push(params)
			// console.log(this.resdata);
			this.getReverseShellList()
	      
	    },
	    // Apply
	    handleUpdate(row) {
	      // Apply
	      if (row.isEdit) {
	        for (let key in row) {
	          if (row[key] === '' || row[key] === null || typeof row[key] === 'undefined') {
	            return
	          }
	        }
	        const params = {
	          name: row.name,
	          inIp: row.inIp,
	          inPort: parseInt(row.inPort),
	          outPort: parseInt(row.outPort),
	          enabled: row.enabled
	        }
	        updateShell(params).then(res => {
	          if (res.code === 200) {
	            this.msgSuccess('Update reverse shell successfully')
	            this.getReverseShellList()
	          }
	        })
	      } else {
	      	// 
	        row.isEdit = true
	      }
	    },
	    // 
	    handleDelete(row) {
	      // 
	      if (row.isEdit) {
	        this.getReverseShellList()
	      } else {
	      	// 
	        this.$confirm('Are you sure to delete reverse shell?', 'warning', {
	          confirmButtonText: 'Sure',
	          cancelButtonText: 'Cancel',
	          type: 'warning'
	        }).then(() => {
	          deleteShell(row.name).then(res => {
	            if (res.code === 200) {
	              this.msgSuccess('Delete reverse shell successfully')
	              this.getReverseShellList()
	            }
	          })
	        }).catch(() => {
	        })
	      }
	    },
		xinz(){
			 this.tableData.push({
	            name: '',
	            inIp: '',
	            inPort: null,
	            outPort: null,
	            enabled: null,
	            isEdit: false
		})
		}
	}
}
</script>
 
<style lang = "scss" scoped>
    
</style>