<template>
    <div>
      <canvas id="canvas" width="600" height="300">
    canvas
</canvas>
<canvas id="canvas1" width="600" height="300"></canvas>
    </div>
</template>

<script>
export default {
    data () {
        return {
            restaurants: [],
            state: '',
            timeout:  null
        }
    },
    methods: {
     
    },
    mounted(){
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        //  const ctx = canvas.getContext('2d',{alpha:true});   
        //2D3D  2D
        // 
        //  var ctx = canvas.getContext('2d'); // canvas
        ctx.strokeStyle = 'red'; 
        // 
        ctx.beginPath();
        ctx.moveTo(0, 0);
    
        ctx.lineTo(105, 0);
  
        ctx.lineTo(0, 105);
        ctx.fill(); // 

        // 
        ctx.beginPath();
        ctx.moveTo(125, 30);
        ctx.lineTo(125, 235);
        ctx.lineTo(235, 30);
        ctx.closePath(); // 
        ctx.stroke();

        ctx.beginPath();
        ctx.moveTo(12, 30);
        ctx.quadraticCurveTo(1230, 80, 130, 70);
        ctx.quadraticCurveTo(115, 730, 115, 50);
        ctx.quadraticCurveTo(1145, 304, 155, 30);
        ctx.quadraticCurveTo(195, 30, 1595, 50);
        ctx.quadraticCurveTo(1595, 70, 1555, 730);
        ctx.quadraticCurveTo(1355, 90, 120, 910);
        ctx.stroke();
        // cxt.fill('#cfc')
        //  ctx.fillStyle = '#cfc'; // 
        //  ctx.fillRect(50, 50, 200, 400);

        const canvas1 = document.getElementById('canvas1');
        const cxt = canvas1.getContext('2d');
              cxt.strokeStyle = 'red'; 
        for (let i = 0; i < 12; i++) {
            cxt.beginPath();
            let x = 25 + i * 50;
            let y = 25;
            let radius = 20;
            let startAngle = 0;
            let endAngle = Math.PI + (Math.PI * i) / 12; // 

            cxt.arc(x, y, radius, startAngle, endAngle);
            cxt.fill();
           }
        }

        // ctx.strokeStyle = 'yellow'; 
}
</script>
