</template>

<script>
export default {
    data () {
        return {
           htmltypeselet:null,
           typeselet:null,
           htmldata:null,
           //
           htmltypeseletoptions:[
             {
                value:1,
                label:'Vue2'
             },
             {
                value:2,
                label:'Vue3'
             },
             {
                value:3,
                label:'Ant Dess 2'
             },
             {
                value:4,
                label:'Ant Dess 3'
             },
           ],
           typeseletoptions:[
             {
                value:1,
                label:'form',
             },
             {
                value:2,
                label:'table',
             },
             {
                value:3,
                label:'Dialog + form',
             },
             {
                value:4,
                label:'Dialog + table',
             },
           ],
           //
           zjtypeshow:null,   
           kongge:`\n`+ "\u00A0".repeat(20),
           //form  
           formData:{
            form:{
                formref:'queryParams',
                querqx:true
            },
            formitem:[{
                 label:'',   
                 zzd:'hospitalid',
                 kjlx:'0',
           }]
           },
           //table
           tableData:{
            table:{
                tableref:'biaoti',
                eitd:false
            },
            tableitem:[{
                label:'',   
                data:'hospitalid',
                width:'50',
                kjlx:'0'
            }]
           },
           //Dialogform  
           dialogform:{
            form:{
                formref:'queryParams',
                querqx:true,
                xyshuj:'isshow',
                quxffm:'handleClose'
            },
            dialogformitem:[{
                 label:'',   
                 zzd:'hospitalid',
                 kjlx:'0'
           }]
           },
           //table
           dialogtable:{
            table:{
                tableref:'biaoti',
                eitd:false,
                xyshuj:'isshow',
                quxffm:'handleClose'
            },
            tableitem:[{
                label:'',   
                data:'hospitalid',
                width:'50',
                kjlx:'0'
            }]
           },
        }
    },
    methods:{
        //
        sus(){
            //Vue2
            if (this.htmltypeselet==1) {
               //form
              if (this.typeselet==1) {
                let formitem = this.formData.formitem.reduce((pet,itme,index)=>{
                    let kjlxinput = `   <el-input style="margin-left: 10px" v-model="${this.formData.form.formref}.${itme.zzd}" size="mini"></el-input>`
                    let kjlxselect = `   <el-select v-model="${this.formData.form.formref}.${itme.zzd}" placeholder="${itme.label}" size="mini" clearable>
                            <el-option
                                v-for="item in "
                                :key="item.xxx"
                                :label="item.xxx"
                                :value="item.xxx">
                                </el-option>
                        </el-select>`
                    let adddata = `<el-form-item label="${itme.label}" prop ="${itme.zzd}" >
                    ${itme.kjlx=='0'?kjlxinput:kjlxselect}
                    </el-form-item>` 
                    if ( this.formData.formitem.length-1!= index) {
                        adddata = adddata + this.kongge
                    }
                   return pet + adddata
                },'')
                 //
                 this.htmldata = `<el-form :model="${this.formData.form.formref}"  size="mini" :inline="true" ref="${this.formData.form.formref}">
                    ${formitem}${this.formData.form.querqx?this.kongge+`<el-button type="primary" icon="el-icon-search" size="mini"  @click="searchForm"></el-button>
                    <el-button size="mini" icon="el-icon-refresh" @click="resetForm"></el-button>`:''}
                </el-form>`
              }//table
              else if(this.typeselet==2){
                let tableitem = this.tableData.tableitem.reduce((pet,itme,index)=>{
                    let kjlxinput = `${this.kongge}<template slot-scope="scope">${this.kongge}<el-input style="margin-left: 10px" v-model="scope.row.${itme.data}" size="mini"></el-input>${this.kongge}</template>`
                    let kjlxselect = `${this.kongge}<template slot-scope="scope">${this.kongge}<el-select v-model="scope.row.${itme.data}" placeholder="${itme.label}" size="mini" clearable>
                            <el-option
                                v-for="item in "
                                :key="item.xxx"
                                :label="item.xxx"
                                :value="item.xxx">
                                </el-option>
                    </el-select>${this.kongge}</template>`
                    let adddata = `
                    <el-table-column
                        prop="${itme.data}"
                        label="${itme.label}"
                        width="${itme.width}">
                        ${this.tableData.table.eitd?(itme.kjlx=='0'?kjlxinput:kjlxselect):``}
                    </el-table-column>` 
                    return pet + adddata
                },'')
                this.htmldata = `<el-table
                    :data="${this.tableData.table.tableref}"
                    ref="${this.tableData.table.tableref}"
                    style="width: 100%">${tableitem}
                </el-table>`
              }//Dialog + form
              else if(this.typeselet==3){
                let formitem = this.dialogform.dialogformitem.reduce((pet,itme,index)=>{
                    let kjlxinput = `   <el-input style="margin-left: 10px" v-model="${this.dialogform.form.formref}.${itme.zzd}" size="mini"></el-input>`
                    let kjlxselect = `   <el-select v-model="${this.dialogform.form.formref}.${itme.zzd}" placeholder="${itme.label}" size="mini" clearable>
                            <el-option
                                v-for="item in "
                                :key="item.xxx"
                                :label="item.xxx"
                                :value="item.xxx">
                            </el-option>
                        </el-select>`
                    let adddata = `<el-form-item label="${itme.label}" prop ="${itme.zzd}" >
                    ${itme.kjlx=='0'?kjlxinput:kjlxselect}
                    </el-form-item>` 
                    if ( this.dialogform.dialogformitem.length-1!= index) {
                        adddata = adddata + this.kongge
                    }
                   return pet + adddata
                },'')
                 //
                 this.htmldata = `
                 <el-dialog
                    title=""
                    :visible.sync="${this.dialogform.form.xyshuj}"
                    width="30%"
                    :before-close="${this.dialogform.form.quxffm}">
                    <el-form :model="${this.dialogform.form.formref}"  size="mini" :inline="true" ref="${this.dialogform.form.formref}">
                        ${formitem}
                    </el-form>
                    <span slot="footer" class="dialog-footer">${this.dialogform.form.querqx?this.kongge+`    <el-button @click="${this.dialogform.form.quxffm}"> </el-button>
                        <el-button type="primary" @click="Stbums"> </el-button>`:''}
                    </span>
                </el-dialog>
                `
              }//Dialog + table  
              else if(this.typeselet==4){
                let tableitem = this.dialogtable.tableitem.reduce((pet,itme,index)=>{
                    let kjlxinput = `${this.kongge}<template slot-scope="scope">${this.kongge}<el-input style="margin-left: 10px" v-model="scope.row.${itme.data}" size="mini"></el-input>${this.kongge}</template>`
                    let kjlxselect = `${this.kongge}<template slot-scope="scope">${this.kongge}<el-select v-model="scope.row.${itme.data}" placeholder="${itme.label}" size="mini" clearable>
                            <el-option
                                v-for="item in "
                                :key="item.xxx"
                                :label="item.xxx"
                                :value="item.xxx">
                                </el-option>
                    </el-select>${this.kongge}</template>`
                    let adddata = `
                    <el-table-column
                        prop="${itme.data}"
                        label="${itme.label}"
                        width="${itme.width}">${this.dialogtable.table.eitd?(itme.kjlx=='0'?kjlxinput:kjlxselect):``}
                    </el-table-column>` 
                    return pet + adddata
                },'')
                this.htmldata = ` <el-dialog
                    title=""
                    :visible.sync="${this.dialogtable.table.xyshuj}"
                    width="30%"
                    :before-close="${this.dialogtable.table.quxffm}">
                    <el-table
                    :data="${this.dialogtable.table.tableref}"
                    ref="${this.dialogtable.table.tableref}"
                    style="width: 100%">${tableitem}
                    </el-table>
                    <span slot="footer" class="dialog-footer">${this.kongge}    <el-button @click="${this.dialogtable.table.quxffm}"> </el-button>
                        <el-button type="primary" @click="Stbums"> </el-button>
                    </span>
                </el-dialog>`
             }  
            }
            if (this.htmltypeselet==2) {
              
                


            }
        },
        // json
        jsondx(){
            if (this.htmltypeselet==1) {
                if (this.typeselet==1){
                   this.htmldata = JSON.stringify(this.formData.formitem[0])
                }
                else if(this.typeselet==2){
                    this.htmldata = JSON.stringify(this.tableData.tableitem[0])
                }
                else if(this.typeselet==3){
                    this.htmldata = JSON.stringify(this.dialogform.tableitem[0])
                }
                else if(this.typeselet==4){
                    this.htmldata = JSON.stringify(this.dialogtable.tableitem[0])
                }
            }
            if (this.htmltypeselet==2) {
              
            }
        },
        //
        zjh(){
            if (this.htmltypeselet==1) {
                if (this.typeselet==1){
                    let datas = {
                        label:'',
                        zzd:'hospitalid',
                        kjlx:'0',
                    } 
                    this.formData.formitem.push(datas)
                }
                else if(this.typeselet==2){
                    let datas = {
                        label:'',   
                        data:'hospitalid',
                        width:'50',
                        kjlx:'0'
                    }
                    this.tableData.tableitem.push(datas)
                }
                else if(this.typeselet==3){
                    let datas = {
                        label:'',   
                        zzd:'hospitalid',
                        kjlx:'0'
                    }
                    this.dialogform.dialogformitem.push(datas)
                }
                else if(this.typeselet==4){
                    let datas = {
                        label:'',   
                        data:'hospitalid',
                        width:'50',
                        kjlx:'0'
                    }
                    this.dialogtable.tableitem.push(datas)
                }
            }
            if (this.htmltypeselet==2) {
              
            }
        
        },
        //
        sch(){
            if (this.htmltypeselet==1) {
                if (this.typeselet==1){
                    if (this.formData.formitem.length<=1) {
                        return
                    }
                    this.formData.formitem.pop()
                }
                else if(this.typeselet==2){
                    if (this.tableData.tableitem.length<=1) {
                        return
                    }
                    this.tableData.tableitem.pop()
                }
                else if(this.typeselet==3){
                    if (this.dialogform.dialogformitem.length<=1) {
                        return
                    }
                    this.dialogform.dialogformitem.pop()
                }
                else if(this.typeselet==4){
                    if (this.dialogtable.tableitem.length<=1) {
                        return
                    }
                    this.dialogtable.tableitem.pop()
                }
            }
            if (this.htmltypeselet==2) {
              
            }
        },
        //
        zjtype(e){    
            this.zjtypeshow =e     
        },
        //cope
        copy(){
           this.copyToClipboard(this.htmldata)
        },
        //cope
        copyToClipboard(str){
            const el = document.createElement('textarea');
            el.value = str;
            el.setAttribute('readonly', '');
            el.style.position = 'absolute';
            el.style.left = '-9999px';
            document.body.appendChild(el);
            const selected =
                document.getSelection().rangeCount > 0 ? document.getSelection().getRangeAt(0) : false;
            el.select();
            document.execCommand('copy');
            document.body.removeChild(el);
            if (selected) {
                document.getSelection().removeAllRanges();
                document.getSelection().addRange(selected);
            }
            alert('');
        }
    }
}
</script>
 
<style lang = "scss" scoped>
::v-deep .tbs{
        .el-input__inner{
            padding: 0;
            margin: 0;
        }
    }
</style>