<template>
    <div>
      <button @click="renderComponent('A')">A</button>
      <button @click="renderComponent('B')">B</button>
      <button @click="renderComponent('C')">C</button>
      <button @click="renderComponent('D')">D</button>
      <button @click="renderComponent('E')">E</button>
      <button @click="renderComponent('F')">F</button>
      <button @click="renderComponent('G')">G</button>
      <button @click="renderComponent('H')">H</button>
      <button @click="renderComponent('I')">I</button>
      <keep-alive >
            <component :is="currentComponent" ></component>
      </keep-alive>
    </div>
  </template>
  
  <script>
  import ComponentA from '@/components/echarts.vue';
  import ComponentB from '@/components/tree.vue';
  import ComponentC from '@/components/ComponentC';
  import ComponentD from '@/components/ComponentD';
  import ComponentE from '@/components/ComponentE';
  import ComponentF from '@/components/ComponentF';
  import ComponentG from '@/components/ComponentG';
  import ComponentH from '@/components/ComponentH';
  import ComponentI from '@/components/ComponentI';
  
  export default {
    data() {
      return {
        currentComponent: null
      };
    },
    methods: {
      renderComponent(e) {
        switch (e) {
          case "A":
          this.currentComponent = ComponentA;
            break;
          case "B":
          this.currentComponent = ComponentB;
            break;
          case "C":
          this.currentComponent = ComponentC;
            break;
          case "D":
          this.currentComponent = ComponentD;
            break;
          case "E":
          this.currentComponent = ComponentE;
            break;
          case "F":
          this.currentComponent = ComponentF;
            break;
          case "G":
          this.currentComponent = ComponentG;
            break;
          case "H":
          this.currentComponent = ComponentH;
            break;
          case "I":
          this.currentComponent = ComponentI;
            break;
          default:
            break;
        }
    
      },
       }
  };
  </script>