<template>
  <div class="flex">
    <div class="left">
      <el-table
      :data="tableData"
      style="width: 100%"
      height="100%"
      border
      @row-contextmenu="handleRowContextMenu"
      >
      <el-table-column
        prop="date"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="">
      </el-table-column>
    </el-table>
    </div>
    <div class="right" style="display:flex">
      <el-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab">
        <el-tab-pane
          v-for="(item, index) in editableTabs"
          :key="item.name"
          :label="item.title"
          :name="item.name"
        >
          {{item.content}}
        </el-tab-pane>
      </el-tabs>
      <el-button
          size="mini"
          style="height:40px"
          type="primary"
          icon="el-icon-plus"
          @click="addTab(editableTabsValue)"
        >
        
        </el-button>
    </div>
    <Youjian ref="youjian" :menu="menu" @aaa="aaa" @bbb="bbb" @ccc="ccc"></Youjian>
  </div>
</template>
<script>
import Youjian from './youjian'
import Scyoujian from './scyoujian'
export default {
  components:{
    Youjian,Scyoujian
  },
  data () {
    return {
      editableTabsValue: '2',
        editableTabs: [{
          title: 'Tab 1',
          name: '1',
          content: 'Tab 1 content'
        }],
        tabIndex: 1,
        tableData: [{
            date: '2016-05-02',
            name: '',
            address: ' 1518 '
          }, {
            date: '2016-05-04',
            name: '',
            address: ' 1517 '
          }, {
            date: '2016-05-01',
            name: '',
            address: ' 1519 '
          }, {
            date: '2016-05-03',
            name: '',
            address: ' 1516 '
          }],
        //
        menu:[
          {
            name:'1',
            index:0,
            methodsname:'aaa',
            isEdit:true
          },
          {
            name:'2',
            index:1,
            methodsname:'bbb',
            isEdit:false
          },
          {
            name:'3',
            index:2,
            methodsname:'ccc',
            isEdit:true
          }
        ],
        tableData: [
        { name: 'John', age: 20 },
        { name: 'Jane', age: 25 },
        { name: 'Tom', age: 30 },
        ],}
    },
    created(){
    },
    methods: {
      addTab(targetName) {
        let newTabName = ++this.tabIndex + '';
        this.editableTabs.push({
          title: `Tab ${this.editableTabs.length+1}`,
          name: newTabName,
          content: `Tab ${this.editableTabs.length+1} content`
        });
        this.editableTabsValue = newTabName;
      },
      removeTab(targetName) {
        let tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }
        
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(tab => tab.name !== targetName);
      },
      handleRowContextMenu(row, column, event){
          // 
          this.$refs.youjian.isShow(row, column, event)
      },
      aaa(e){
              console.log(e,'1');
      },
      bbb(e){
              console.log(e,'2');
      },
      ccc(e){
              console.log(e,'3');
      }
    }
  
}
</script>
 
<style lang="less">
.flex{
  display:flex;
}
.left{
  width:26vw;
  height:calc(100vh - 30px);
  border:1px solid#000;

}
.right {
  width:74vw;
  height:calc(100vh - 30px);
  border:1px solid#000;
}

</style>