<template>
  <div ref="container"></div>
</template>

<script>
import * as THREE from 'three';

export default {
  name: 'ThreeScene',
  mounted() {
    const container = this.$refs.container
    // 3DScene
    const scene = new THREE.Scene();
    //Geometry
    const geometry = new THREE.BoxGeometry(50, 20, 30); 
    //Material
    const material = new THREE.MeshBasicMaterial({
        color: 0xff0000,//0xff0000
    }); 
    // geometrymaterial
    const mesh = new THREE.Mesh(geometry, material); //Mesh
    //
    mesh.position.set(0,60,0);
    //3D
    scene.add(mesh); 
    // 
    // const camera = new THREE.PerspectiveCamera();
    // widthheightThree.jsCanvas(px)
    const width = 800; //
    const height = 500; //
    // 30:, width / height:Canvas, 1:, 3000
    const camera = new THREE.PerspectiveCamera(30, width / height, 1, 3000);
    //Three.js
    // 
    camera.position.set(200, 200, 200); 
    //Threejs 3D
    //camera.lookAt(0, 0, 0); //
    // camera.lookAt(0, 10, 0);  //y10
    camera.lookAt(mesh.position);//mesh
    // 
    const renderer = new THREE.WebGLRenderer();
    // // threejs(:px)
    // const width = 800; //
    // const height = 500; //
    renderer.setSize(width, height); //three.js(px)
    renderer.render(scene, camera); //
    container.appendChild(renderer.domElement);
    // this.init();
    // this.createScene();
    // this.createCamera();
    // this.addObjects();
    // this.startAnimationLoop();
  },
  methods: {
    // init() {
